@import url(https://fonts.googleapis.com/css?family=Fira+Sans+Extra+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css?family=Dancing+Script&disp);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.topLandingPAge {
  background-image: url("/images/30861.jpg");
  background-repeat: no-repeat;
  background-position: right top;
  background-size: cover;
  padding: 70px 0 5px 0;
}
.nombreMila{
  font-family: 'Fira Sans Extra Condensed', sans-serif;
  font-size: 80px;
  color: black;
  letter-spacing: 5px;
  margin-top: 20px;
  text-align: center;
}
.pensamientosMila{
  color: #702011;
  text-align: center;
  font-family: 'Dancing Script', cursive;
  font-size: 40px;
  margin-top: 10px
}
.landingBody
 {
  background-color: #f0a42f;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Fira Sans Extra Condensed', sans-serif;
  color: white;
  font-size: 30px;
  padding-bottom: 50px;
}
.information{
  padding: 50px 20px 20px 20px;
  justify-content: center;
}
.messageBottom{
  padding-bottom: 40px;
}
.button{
  display: inline-block;
  background-color: #702011;
  color: white;
  font-size: 18px;
  text-align:center;
  touch-action: manipulation;
  cursor: pointer;
  border: 1px solid transparent;
  padding: 10px 12px 5px 20px;
  border-radius: 4px;
  font-family: 'Fira Sans Extra Condensed', sans-serif;
}
